import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        token: ''
    },
    mutations: {
        Login: function Login(state, token) {
            state.token = token;
        },
        USERNAME: function USERNAME(state, userName) {
            state.userName = userName;
        }
    }
});


